<template>
  <section v-if="!isLoading">
    <section v-if="$store.state.cart.length !== 0">
      <Step/>
      <router-view></router-view>
    </section>
    <section v-else class="text-center py-6">
      <h1>你的購物車是空的喔</h1>
    </section>
  </section>
</template>
<script>
import Step from "@/views/frontEnd/components/CheckStep.vue";
import AxiosClass from "@/config/axiosClass.js";
export default {
  components: {
    Step
  },
  computed: {
    isLoading() {
      return this.$store.state.isLoading;
    }
  },
  methods: {
    checkCookie() {
      var cookieValue = document.cookie.replace(
        /(?:(?:^|.*;\s*)user_email\s*\=\s*([^;]*).*$)|^.*$/,
        "$1"
      );
      cookieValue = decodeURIComponent(cookieValue);
      if (!cookieValue) {
        this.$router.push("/login");
      }
    }
  },
  created() {
    this.checkCookie();
  },
  watch: {
    $route(to, from) {
      this.checkCookie();
    }
  }
};
</script>